$color-primary: #008AD8;

.wp-block-wisdm-learndash-reports-quiz-reports {
    background: #FFFFFF;
    box-shadow: 1px 2px 3px rgb(0 0 0 / 10%);
    border-radius: 4 px;
    margin: 0 30px;
    padding: 30px;
    min-height: 790px ;
}

.wp-block-wisdm-learndash-reports-quiz-reports .qre-breadcrumbs {
    display: none;
}

.wp-block-wisdm-learndash-reports-quiz-reports .qre-reports-content {
    padding: 0;
    margin: 0;
}

.course-list-table-container {
    padding: 2px;
    margin: 10px;
    margin-top: 0;
}

.course-list-table thead{
    background-color: #444444;
    color: #ffffff;
}

.course-list-table tr td {
    border: 0px;
    border-bottom: 1px solid #E5E5E5;
    padding: 10px;
}

table.course-list-table thead th {
    text-align: left;
}

table.course-list-table {
    border-radius: 2px;
    border: none;
    width: 100%;
}

table.course-list-table tr {
    line-height: 30px; 
}

.course-list-table-download-options {
    text-align: right;
    margin-bottom: 22px;
}

tr.course-list-table-data-row:hover{
    cursor:pointer;
    border-radius: 2px;
    transform: scaleX(1.01);
    transition: all 0.2s ease-in-out;
    box-shadow: 0px 4px 20px 0px #00000033;
    background-color: #fff;
    transform: scale(1.01);
}

.edit-post-visual-editor table.course-list-table{
    border-radius: 2px;
    border: none;
    width: 100%;
}

.wisdm-ld-loading {
    width: 100%;
    min-height: 40px;
    animation: load 1.2s infinite 0s ease-in-out;
	animation-direction: alternate;
	text-shadow: 0 0 1px $color-primary;
    display:flex;
	justify-content: center;
	align-items: center;
    @keyframes load {
        0%{
            opacity: 0.08;
            filter: blur(5px);
            letter-spacing: 3px;
            }
        100%{
            
            }
    }
}

.download-csv-button {
    color:#ffffff;
}

.wisdm-learndash-reports-chart-block.error {
    text-align: center;
}

.wrld-loader {
    display: none;
}

.qre-reports-content .results-section #qre_summarized_data_wrapper {
    overflow: unset;
    overflow-x: scroll;
    &::-webkit-scrollbar {
        height: 10px;
        width: 4px;
        background: rgb(231, 231, 231);
    }

    &::-webkit-scrollbar-thumb:horizontal {
        border-radius: 4px;
        background: #858585;
    }
}